.navbar-form .form-wrapper{position:relative}.navbar-form .form-wrapper .search{position:relative;width:100%;margin-top:5px;padding:7px 15px;border:1px solid #75b8e5;box-shadow:2px 2px 2px #445 inset;color:#fff;background:#0c4f7c}.navbar-form .form-wrapper .icon{position:absolute;right:10px;top:16px}.contact{height:640px;position:relative}.contact #googlemaps{position:absolute;top:0;left:0;width:100%;height:640px}.contact .contact-field{position:absolute;top:40px;left:47%;height:auto;z-index:9999;padding:30px 40px;background-color:#192732}.contact .contact-field address ul li{list-style:none;font-family:Arial,sans-serif}.contact .contact-field address ul li:nth-of-type(2),.contact .contact-field address ul li:nth-of-type(5){margin-bottom:20px}.contact .contact-field h1.title{margin-bottom:65px}.contact .contact-field form{margin-top:105px}.contact .contact-field .form-control{margin-bottom:10px;border:1px solid #fff;border-radius:0;box-shadow:2px 2px 3px rgba(63,53,54,.3) inset;resize:none}.contact .contact-field textarea{height:190px}.contact .error-message{color:#fff;display:inline-block;padding:0 10px;margin-bottom:5px}@media screen and (max-width:992px){.contact .contact-field{left:40px;right:40px}}